<template>
  <div class="home">
    <div class="header clearfix">
      <div class="fl calendar" 
      @click="Calendar(data.dates)">
        <!-- ({name: 'calendar', date: {id: data.date}}) -->
        <span></span>
      </div>
      <div class="fr classify">
        <span></span>
      </div>
    </div>

    <!-- 今天天气 -->
    <div class="current-weather">
      <div class="clearfix date-week">
        <span class="fl date">{{data.date}}</span>
        <span class="fl week">{{data.week}}</span>
      </div>
      <div class="tem-box clearfix">
        <span>{{data.tem}}℃</span>
      </div>
      <div class="air-quality clearfix">
        <div class="quality fl clearfix">
          <span class="fl"></span>
          <span class="fl">{{data.wea}}</span>
        </div>
        <div class="pm fl">PM：{{data.air_level}}</div>
      </div>
      <div class="location clearfix">
        <span class="fl"></span>
        <span class="fl">{{data.city}}</span>
      </div>
    </div>

    <!-- 未来6天天气 -->
    <div class="weather">
    <div class="week-weather">
      <ul class="clearfix future-forecast">
        <li ref="weather" v-for="(item, index) in dataed" :key="index">
          <div class="day">{{item.today}}</div>
          <div class="tem">{{item.tem2}}℃~{{item.tem1}}℃</div>
          <div class="wea_day clearfix">
            <span class="wea fl"></span>
            <span class="wea-img fl">{{item.wea_day}}</span>
          </div>
          <div class="win">{{item.win[0]}}</div>
        </li>
      </ul>
    </div>
    </div>
  </div>
</template>

<script>
import $ from 'jquery'

export default {
  name: "home",

  data() {
    return {
      params: {
        appid: '34761866',
        appsecret: 'WbfE5Kn6',
        version: 'v6',
      },
      params1: {
        appid: '34761866',
        appsecret: 'WbfE5Kn6',
        version: 'v9',
      },

      data: {},
      dataed: {}
    }
  },

  methods: {
    Calendar(id) {
      //跳转到calendar页面
      this.$router.push({name: 'calendar', query: {id}});
    }
  },
 

  created() {
    // return;
    $.ajax({
      type: 'POST',
      url: 'https://www.tianqiapi.com/api',
      data: this.params,
      dataType: 'jsonp',
      
    }).then((result) => {

      let dates = result.date.split("-")

      dates[1] = dates[1] <= 10 ? dates[1].substr(1, 1) : dates[1];
      dates[2] = dates[2] <= 10 ? dates[2].substr(1, 1) : dates[2];

      let dated = dates[0] + '-' + dates[1] + '-' + dates[2]
      // console.log(dated)
      result.dates = dated
      // console.log('result :', result);
      this.data = result;
    }).catch((err) => {
      // console.log('出现错误');
    });
      
    // 未来6天天气
    $.ajax({
      type: 'GET',
      url: 'https://www.tianqiapi.com/api',
      data: this.params1,
      dataType: 'jsonp',

    }).then((result) => {

      result.data.forEach(v => {
        v.today = v.day.replace(/（星期[一二三四五六日]）/, '')
      });
      // console.log('resultdddd :', result);
      this.dataed = result.data.slice(1);
      // console.log('this.dataed :', this.dataed)substr
      
    }).catch((err) => {
      // console.log('出现错误');
    });
  }
};
</script>

